<template>
    <div class="yyXiangqin">
      <div class="xianlist">
        <img class="imginfo" src="@/assets/img/yuyuexiangqing--renwu.png" alt="">
        <div class="timechuo">
          <span>{{this.orderInfo.birthday}}</span>
        </div>
        <ul class="liucheng" v-if="orderInfo.process<=4">
        <!--  <li v-if="index < 4" v-for="(item,index) in liuClist" :key="index" >
            <i></i>
            <span>{{item}}</span>
          </li>-->
          <li v-if="index<=orderInfo.process" v-for="(item,index) in liuClist" :key="index">
            <div class="status">
              <div class="lian">
                <div class="xian"></div>
                <div class="dian" :class="index==orderInfo.process?'dianbig':''"></div>
                <div class="xian"></div>
              </div>
              <div :class="index==orderInfo.process?'statusDetail':''">
                {{item}}
              </div>
            </div>
          </li>
        </ul>
        <ul class="liucheng errlist"  v-if="orderInfo.close_process==1">
          <li v-for="(item,index) in errListL1" :key="index">
            <div class="status">
              <div class="lian">
                <div class="xian"></div>
                <div class="dian"></div>
                <div class="xian"></div>
              </div>
              <div :class="index==orderInfo.process?'statusDetail':''">
                {{item}}
              </div>
            </div>
          </li>
        </ul>
        <ul class="liucheng errlist"  v-if="orderInfo.close_process==2">
          <li v-for="(item,index) in errListL2" :key="index">
            <div class="status">
              <div class="lian">
                <div class="xian"></div>
                <div class="dian"></div>
                <div class="xian"></div>
              </div>
              <div :class="index==orderInfo.process?'statusDetail':''">
                {{item}}
              </div>
            </div>
          </li>
        </ul>
      </div>
      <div class="yudetail">
        <div class="yuexiang">
          <div class="yueimg">
            <img src="../assets/img/timg.jpg" alt="">
          </div>
          <div class="yuecont">
            <h3 class="danyi">{{orderInfo.ins_name}} </h3>
            <p>{{orderInfo.brief }} </p>
          </div>
        </div>
        <div class="infomation">
             <h4>预约人信息</h4>
            <div class="moreinfo">
              <div>
                姓名 <span> {{orderInfo.nickname}}</span>
              </div>
              <div>
                联系方式 <span>{{orderInfo.cellphone}}</span>
              </div>
              <div>
                有无社保 <span>{{orderInfo.is_bao==0?'无':'有'}}</span>
              </div>
            </div>
        </div>
        <div class="otherbx">
          <h3>其他可选保险</h3>
          <ul class="otherlist">
              <router-link tag="li" :to="{path:'/xiangqin',query:{'id':item.id}}" v-for="(item,index) in otherlist" :key="index">
                <div class="imgwrap">
                  <img :src="item.imgs" alt="">
                </div>
                <h4 class="danyi">{{item.title}}</h4>
                <div class="otherinfo">
                  <div class="yuyue"><span>{{item.yu_num}}</span>人已预约</div>
                </div>
              </router-link>
          </ul>
        </div>
      </div>
    </div>
</template>

<script>
import { apiGet } from '../common/js/api';
    export default {
        name: "yy_xiangqin",
      data(){
        const query = this.$route.query
          return{
            orderId:query.id,//预约Id
            accessToken:'',
            inse_id:'',
            mark:'',
            liuClist:['待联系',"预约成功","已见面","已签约","已返款"],
            errListL1:['待联系',"预约成功","已取消"],
            errListL2:['待联系',"预约成功","已见面","已取消"],
            otherlist: [],
            orderInfo:{

            }//预约详情信息
          }
      },
      created(){
           this.accessToken = this.$cookie.get('accessToken');
       /* this.accessToken='9RVRcGJsjs7BzwvRTN5ZFgdQHleRj5z8';*/
        this.inse_id=this.$route.params.id,
          this.mark=this.$route.params.mark,
          console.log(this.inse_id,this.mark)
        this.getOrderDetail()
        this.getOtherList()
      },
      methods:{
        //获取预约详情信息
        getOrderDetail(){
          apiGet("/user/inserve-detail",{accessToken:this.accessToken,inse_id:this.inse_id,mark:this.mark}).then(res=>{
            console.log(res)
            this.orderInfo = res.data.data.detail;
            console.log(this.orderInfo.process)
            this.otherlist=res.data.data.others;
            console.log( this.otherlist )
          })
        },
        //获取相关保险列表
        getOtherList(){

        }
      }
    }
</script>

<style lang="scss" scoped>
  .yyXiangqin{
    height: 100%;
    overflow: auto;
    background: #E6E9ED;
    .xianlist{
      width: 3.5rem;
      height: 2.11rem;
      background:linear-gradient(-90deg,rgba(127,173,249,1),rgba(70,133,238,1));
      border-radius:0.04rem;
      margin: 0.17rem auto;
      position: relative;
      .imginfo{
        position: absolute;
        width: 0.8rem;
        height: 0.8rem;
        right: 0.63rem;
        bottom: 0.24rem;
      }
      .timechuo{
        padding-top: 0.22rem;
        margin-left: 0.47rem;
        color: white;
        font-size: 0.15rem;
      }
      .liucheng{
        margin-top: 0.17rem;
        margin-left: 0.28rem;
        li{color: white;
          .liuTime,.status{
            display: flex;
            width: 2rem;
            margin-bottom: 0.12rem;
          }
          .lian{
            height: 0.2rem;
            width: 0.16rem;
            position: relative;
            text-align: center;
            margin-right: 0.15rem;
            .dian{
              width: 0.04rem;
              height: 0.04rem;
              border-radius: 0.04rem;
              background: white;
              position: absolute;
              z-index: 3;
              top: 50%;
              left: 50%;
            }
            .dianbig{
              width: 0.08rem;
              height: 0.08rem;
              left: 40%;
              border-radius: 0.08rem;
            }
            .xian{
              height: 50%;
              width: 1px;
              top:0;
              color: rgba(255,255,255,.3);
              position: absolute;
              left: 60%;
              text-align: center;
              background: white;
            }
            .xian:last-of-type{
              height: 159%;
              width: 1px;
              left: 60%;
              top:60%;
              color: rgba(255,255,255,.3);
              position: absolute;
              text-align: center;
              background: white;
            }
          }
          .statusDetail{
            font-size: 0.17rem;
          }
          .statusDetails{
            font-size: 0.19rem;
            color: white;
          }
          .statusDetail{
            height: 0.3rem;
            line-height: 0.3rem;
          }
        }
        li:first-of-type{
          .liuTime{
            .lian{
              visibility: hidden
            }
          }
          .status{
            .lian{
              .xian:first-of-type{visibility: hidden}
            }
          }
        }
        li:last-of-type{
          .status{
            .lian{
              .xian:last-of-type{visibility: hidden}
            }
          }
        }
      }
      .errlist{
        li:last-of-type{
          font-size: 0.19rem;
          color: white;
          .dian{
            width: 0.08rem;
            height: 0.08rem;
            left: 40%;
            border-radius: 0.08rem;
          }
        }
      }
    }
    .yudetail{
      width: 3.5rem;
      margin: 0.18rem auto;
      background: white;
      .yuexiang{
        display: flex;
        padding: 0.21rem 0.13rem;
        margin-bottom: 0.21rem;
        border-bottom: 1px solid #f5f5f5;
        .yueimg{
          width: 0.73rem;
          border-radius: 0.04rem;
          justify-content: space-between;
          img{
            width: 0.73rem;
            height: 0.73rem;
          }
        }
        .yuecont{
          margin-left: 0.13rem;
          h3{
            font-size: 0.17rem;
          }
          p{
            font-size: 0.14rem;
            color: #9a9a9a;
          }
        }
      }
      .infomation{
        padding: 0 0.13rem 0.13rem;
        h4{
          font-size: 0.14rem;
          color: #9a9a9a;
        }
        .moreinfo{
          width: 3.25rem;
          margin: 0 auto;
          background: #E5E8EB;
          border-radius: 0.04rem;
          padding: 0.16rem 0.13rem;
          div{
            display: flex;
            justify-content: space-between;
            color: #9a9a9a;
            font-size: 0.17rem;
            line-height: 0.3rem;
          }
        }
      }
    }
    .otherbx{
      width: 3.5rem;
      margin: 0.17rem auto;
      padding-top: 0.17rem;
      background: white;
      h3{
        width: 1.28rem;
        height: 0.31rem;
        background: #EEA95F;
        font-size: 0.17rem;
        color: white;
        border-radius:0px 0.16rem 0.16rem 0px;
        line-height: 0.31rem;
        text-align: center;
        margin-bottom: 0.17rem;
      }
      .otherlist{
        display: flex;
        padding:0.13rem;
        flex-wrap: wrap;
        justify-content: space-between;
        li{
          width: 1.56rem;
          margin-bottom: 0.13rem;
          img,.imgwrap{
            width: 1.56rem;
            height: 1.59rem;
          }
          h4{
            font-size: 0.17rem;
            color: #333;
            line-height: 0.3rem;
          }
            .otherinfo{
              display: flex;
              justify-content: space-between;
              .proce{
                font-size: 0.16rem;
                color:red;
              }
              .yuyue{
                font-size: 0.13rem;
                color:#999;
                line-height: 0.3rem;
              }
            }
        }
      }
    }
  }
</style>



